import './css/style.sass'
import map from '../assets/map/map.js'
// import './map'

var audio_mult = 1

var directions = ['n', 'l', 's', 'o']
var walk = {
    l: [1, 0],
    s: [0, -1],
    o: [-1, 0],
    n: [0, 1],
}

var state = {
    position: [2, 3],
    direction: 'n'
}

function posDir(state) {
    return state.position[0] + '_' + state.position[1] + '_' + state.direction
}

function rotLeft(dir) {
    if (dir == 'n') return 'o'
    else return directions[directions.indexOf(dir) - 1]
}

function rotRight(dir) {
    if (dir == 'o') return 'n'
    else return directions[directions.indexOf(dir) + 1]
}

function tryChangePos(dx, dy) {
    let newPos = [state.position[0]+dx, state.position[1]+dy]

    var exist = false
    for (let pos of map){
        // console.log(pos, newPos)
        if (pos[0] == newPos[0] && pos[1] == newPos[1]) {
            // console.log('uhu')
            exist = true
            break
        }
    }

    if (exist) {
        state.position = newPos
        setListenerPos(state.position)
    }
}

function setListenerPos(pos) {
    // game.sound.context.listener.setPosition(newPos[0] * audio_mult, newPos[1] * audio_mult, 1)
    game.sound.context.listener.setPosition(pos[0] * audio_mult, 1, - pos[1] * audio_mult)
}

function setAudioOri() {
    var [x, y] = walk[state.direction]
    // console.log(state.direction, x, y)
    // console.log(state)
    // game.sound.context.listener.setOrientation(0, 0, -1, 0, 0, 1)
    game.sound.context.listener.setOrientation(x, 0, -y, 0, 1, 0)
}

function move(event, key) {
    // console.log('move', event, key)
    switch (key) {
    case 'UP':
            tryChangePos(walk[state.direction][0], walk[state.direction][1])
            break
        case 'DOWN':
            tryChangePos(-walk[state.direction][0], -walk[state.direction][1])
            break
        case 'LEFT':
            state.direction = rotLeft(state.direction)
            setAudioOri()
            break
        case 'RIGHT':
            state.direction = rotRight(state.direction)
            setAudioOri()
            break
    }
    loadVision(state)
}

function setVision() {
    if (state.img) state.img.destroy()
    state.img = game.add.image(0, 0, posDir(state))
    var r = Math.min(game.width/state.img.width, game.height/state.img.height)
    state.img.scale.setTo(r)
    window.i = state.img
    // state.img.tint = 0x0000ff
    state.img.x = (game.width - state.img.width) / 2
    state.img.y = (game.height - state.img.height) / 2

    state.img.filters = state.filters

    // state.img.scale.setTo(0.1)
    // state.img.width = game.width
    // state.img.scale.height = state.img.height * game.width/w
    // state.img.scale.setTo(state.img.width * game.width/w)
}

function loadVision(state) {
    var s = posDir(state)
    if (!game.cache.checkImageKey(s)) {
        game.load.image(s, 'assets/map/' + s + '.png').onFileComplete.add(setVision)
        game.load.start()
    } else {
        setVision()
    }
}


function preload() {
    game.stage.disableVisibilityChange = true
    game.load.audio('stream', ['assets/audio/stream_cold.ogg'])
    game.load.script('gray', 'https://cdn.rawgit.com/photonstorm/phaser/master/filters/Gray.js')
}

function create() {

    game.stage.backgroundColor = '#000000'
    // state.filters = [game.add.filter('Gray')]
    loadVision(state)

    var music = game.add.audio('stream')
    music.loop = true
    console.log(music)
    music.play()
    var panner = game.sound.context.createPanner()

    panner.panningModel = 'HRTF'
    panner.distanceModel = 'inverse'
    panner.refDistance = 1
    panner.maxDistance = 10000
    panner.rolloffFactor = 10

    music.gainNode.disconnect()
    music.gainNode.connect(panner)
    panner.connect(game.sound.masterGain)
    // panner.setPosition(3 * audio_mult, 3 * audio_mult, 0)
    panner.setPosition(3 * audio_mult, 0, - 3 * audio_mult)
    window.p = panner
    window.m = music
    window.g = game

    setListenerPos(state.position)

    var cursors = ['UP', 'DOWN', 'LEFT', 'RIGHT']
        cursors.map(
            (key) => game.input.keyboard.addKey(
                Phaser.Keyboard[key]).onDown.add(move, this, null, key))
}

function update() {
}

var innerWidth = window.innerWidth
var innerHeight = window.innerHeight

var game = new Phaser.Game(innerWidth, innerHeight, Phaser.AUTO, 'app',
                           { preload: preload,
                             create: create,
                             update: update })



